<template>
  <div id="app">
    <nav>
      <router-link to="/">Home</router-link> |
      <router-link to="/oprema">Oprema</router-link> |
      <router-link to="/rezervacija-usluge">Rezervacija usluge</router-link> |
      
      <template v-if="!token">
        <router-link to="/register">Register</router-link> |
        <router-link to="/login">Log In</router-link>
      </template>
      
      <template v-else>
        <router-link @click="logout()">Log Out</router-link>
      </template>
    </nav>
    <router-view/>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex';

export default {
  computed: {
    ...mapState([
      'token'
    ])
  },
  methods: {
    ...mapMutations([
      'removeToken',
      'setToken'
    ]),
    logout() {
      this.removeToken();
    }
  },
  mounted() {
    if (localStorage.token) {
      this.setToken(localStorage.token);
    }
  },
};
</script>

