import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Usluga from '../views/Usluga.vue' 
import Oprema from '../views/Oprema.vue'
import Rezervacija from '@/views/Rezervacija.vue'
import Login from '@/views/Login.vue'
import Register from '@/views/Register.vue'
import { BIconZoomIn } from 'bootstrap-vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/usluga/:id',
    name: 'Usluga',
    component: Usluga
  },
  {
    path: '/oprema',
    name: 'Oprema',
    component: Oprema
  },
  {
    path: '/rezervacija-usluge',
    name: 'Rezervacija',
    component: Rezervacija
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
