import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    oprema: [],
    usluge: [],
    sveUslugeIDs: [],
    token: ''
  },
  getters: {
  },
  mutations: {
    addOprema(state,oprema){
      state.oprema = oprema;
    },
    addUsluga(state,usluga){
      state.usluge[usluga.id] =usluga;
    },
    addSveUslugeIDs(state, niz){
      state.sveUslugeIDs = niz;
    },
    setToken(state, token) {
      state.token = token;
      localStorage.token = token;
    },
    removeToken(state) {
      state.token = '';
      localStorage.token = '';
    },
  },
  actions: {
    async fetchOprema ({commit}){
        fetch('http://localhost:9000/oprema/')
        .then(res => res.json())
        .then(data => commit('addOprema', data));
    },
    async getUsluga({commit, state}, uslugaID){
      return new Promise((resolve, reject)=>{
        if(state.usluge[uslugaID]){
          resolve(state.usluge[uslugaID]);
        }
        else{
          fetch(`http://localhost:9000/usluga/${uslugaID}`)
            .then( res=>res.json() )
              .then( data=> {
                commit('addUsluga', data)
                resolve(data);
              });
        }
    });

  },
  async fetchSveUsluge({commit}){
    fetch(`http://localhost:9000/usluga/`)
      .then( res=>res.json() )
        .then( data=> commit('addSveUslugeIDs', data) );
  },
  async register({ commit }, obj) {
    try {
      const response = await fetch('http://127.0.0.1:9001/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
      });

      const data = await response.json();
      commit('setToken', data.token);
    } catch (error) {
      console.error('Error:', error);
    }
  },

  async login({ commit }, obj) {
    try {
      const response = await fetch('http://127.0.0.1:9001/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
      });

      const data = await response.json();
      if (data.msg) {
        alert(data.msg);
      } else {
        commit('setToken', data.token);
      }
    } catch (error) {
      console.error('Error:', error);
    }
  }
},

  modules: {
  }
})
