<template>
    <div id="app">
      <img src="@/assets/PureShine-logo.png" alt="Logo" class="logo"/>
      <Header :title="headerTitle"/>
      <b-alert :variant="statusnaPorukaTip" :show="statusnaPoruka!=null">
            {{ statusnaPoruka }}
        </b-alert>
        <div>
            <b-container fluid>
                <b-row>
                    <b-col sm="3">
                        <label for="ime">Ime i Prezime</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-input id="ime"
                            :state = "validnoIme"
                            v-model="forma.ime" class="input-stil"></b-form-input>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col sm="3">
                        <label>Adresa</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-input id="adresa"
                            :state = "validnaAdresa"
                            v-model="forma.adresa" class="input-stil"></b-form-input>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col sm="3">
                        <label for="telefon">Telefon</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-input id="telefon" :state="validanTelefon" v-model="forma.telefon" class="input-stil"></b-form-input>
                     </b-col>
                </b-row>
            <b-row>
                 <b-col sm="3">
                     <label for="zakazano-vreme">Zakazano vreme</label>
                 </b-col>
                <b-col sm="9">
                 <b-form-datepicker
                     id="zakazano-vreme"
                    :state="validnoZakazanoVreme"
                    v-model="forma.zakazanoVreme" class="input-stil"></b-form-datepicker>
                </b-col>
            </b-row>
  
            <b-row>
                    <b-col sm="3">
                        <label>Unesite naziv usluge kao i dodatne informacije ili napomene:</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-textarea id="textarea"
                         :state = "validnaPrijava"
                            v-model="forma.prijava"
                            rows="4" class="input-stil"></b-form-textarea>
                    </b-col>
                    
                </b-row>
            </b-container>
            <b-button @click="posalji()" variant="primary" class="btn-stil">Posalji upit</b-button>
            </div>
        </div>
  </template>
  <script>
  import Header from '@/components/Header.vue'
  import { mapActions, mapState } from 'vuex'
  
  export default {
    name: 'App',
    components: {
      Header
    },
    data(){
      return{
        headerTitle: "Rezervisite nasu uslugu",
        statusnaPoruka: null,
        statusnaPorukaTip: null,
        forma:{
            ime: null,
            adresa: null,
            telefon: null,
            zakazanoVreme: null,
            usluga: null,
            prijava: null

        }
      }
    },
    computed:{
      validnoIme(){
        if(this.forma.ime == null) return null;
        else if (this.forma.ime.length > 2) return true
        else return false;
      },
      validnaAdresa(){
        if(this.forma.adresa == null) return null;
        else if(this.forma.adresa.length > 10 && /\d/.test(this.forma.adresa)) return true
        else return false;
      },

      validanTelefon() {
      if (this.forma.telefon == null) return null;
       else if (this.forma.telefon.length === 9 || this.forma.telefon.length === 10) return true;
      else return false;
    },
    validnoZakazanoVreme() {
      if (this.forma.zakazanoVreme == null) return null;
    //   const Date = new Date (this.forma.zakazanoVreme);
    //   const nowDate = new Date();
    //   nowDate.setHours(0,0,0,0);
    //   if (Date <= nowDate)
    //     return false;
      else return true;
    },
    validnaUsluga() {
      if (this.forma.usluga == null) return null;
      else return true;
    },

      validnaPrijava(){
        if(this.forma.prijava == null) return null;
        else if(this.forma.prijava.length > 2) return true 
        else return false;
      }
    },
    methods: {
      posalji(){
        if(this.validnoIme && this.validnaAdresa && this.validanTelefon 
                            && this.validnoZakazanoVreme && this.validnaUsluga){
            fetch("http://localhost:9000/rezervacija-uslugee", {
                headers: {
                    'Content-Type': 'application/json'
                },
                method: "POST",
                body: JSON.stringify(this.forma)
            })
            .then(res => res.json())
            .then(res => {
                console.log(res);
                if(res.error) {
                    this.statusnaPoruka = res.error;
                    this.statusnaPorukaTip = 'danger';
                } else {
                    this.statusnaPoruka = "Upit za rezervaciju usluge je uspesno poslat";
                    this.statusnaPorukaTip = 'success';
                }
            });
        }
        else{
            return;
        }
      }
    }
  }
  </script>
  
  <style>
  @import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');
  
  #app {
    font-family: 'Glock', serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    margin-top: 60px;
    background-color: #502029;
    color: #ea5c70;
  }
  .input-stil {
  margin-bottom: 15px;
}
.btn-stil {
  margin-bottom: 15px;
  background-color: #ea5c70 !important
}
  
  </style>