<template>
   <div>
        <UslugaSingle v-for="usluga in usluge" 
			:key="usluga.id" :usluga="usluga"/>
    </div>

</template>

<script>
import UslugaSingle from '@/components/UslugaSingle.vue';
import { mapActions, mapState } from 'vuex'
export default {
  name: 'UslugeList',
  components: {
    UslugaSingle
  },
  data(){
    return {
        usluge: []
    }
  }, 

  props: {
    uslugeIDs: Array
  },
   mounted() {
        this.uslugeIDs.map( obj => {
            fetch(`http://localhost:9000/usluga/${obj.id}`)
                .then( obj => obj.json())
                .then( res => this.usluge.push(res));})

        // this.getUsluga(obj.id)
              //.then( usluga => this.usluge.push(usluga) );
   },
   methods: {
      ...mapActions(['getUsluga'])
    },

   watch: {
    studentiIDs(nVal) {
        this.usluge = [];
        nVal.map( obj => {
                fetch(`http://localhost:9000/usluga/${obj.id}`)
                    .then( obj => obj.json())
                    .then( res => this.usluge.push(res));
            });
        // this.getUsluga(obj.id)
        //       .then( usluga => this.usluge.push(usluga) );
    }
   }

}
</script>

