<template>
    <div>
        <Header title="Oprema koju koriste nasi zaposleni"></Header>
        <b-pagination
            v-model="currentPage"
            :total_rows="this.oprema.length"
            :per-page="perPage"
            aria-controls="tabelaOprema" ></b-pagination>
    
        <b-table striped hover :items="this.oprema" 
                               :fields="fields"
                               :per-page="perPage"
                               :currentPage="currentPage"
                               id="tabelaOprema"></b-table>
    </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
// import { mapState } from 'vuex';
import Header from '@/components/Header.vue'

export default{ name: 'Oprema', 
                components: { Header},
    data(){
        return{
            fields: [
            {
                key: "naziv",
                sortable: true,
                label: "Oprema"
            }
        ],
        
        perPage:10,
        currentPage:1,
        };
    },
    computed: {
        ...mapState([ 'oprema' ])
        },
    methods: {
      ...mapActions(['fetchOprema'])
  },   
    mounted(){
        this.fetchOprema();
    }
}
</script>

<style>
#tabelaOprema {
  max-width: 800px;  
  margin: auto;    
}


</style>