<template>
    <div id="app">
      <Header title="Log In"/><br>
      <b-form @submit="onSubmit">
        <b-form-group label="User Name:" label-for="name" class="input"><br>
          <b-form-input id="name" v-model="form.name" 
             placeholder="Enter name" required class="sminput"></b-form-input>
        </b-form-group><br>
        <b-form-group label="Password:" label-for="password" class="input"><br>
          <b-form-input id="password" v-model="form.password" 
            type="password" required class="sminput"></b-form-input>
        </b-form-group><br>
        <b-button type="submit" variant="primary" class="logbtn">Login</b-button>
      </b-form>
    </div>
  </template>
  <script>
    import Header from '@/components/Header.vue';
    import { mapActions } from 'vuex';
    export default {
      name: 'Login',
      components: {
        Header
      },
      data() {
        return {
          form: {
            username: '',
            password: ''
          }
        }
      },
      methods: {
        ...mapActions([
          'login'
        ]),
        onSubmit(e) {
          e.preventDefault();
          this.login(this.form);
          this.$router.push({ name: 'Home' });
        }
      }
    }
  </script>
  <style scoped>
  .input {
    text-align: center;
    font-size: large;
    
  }
  
  .sminput {
    width: 200px; 
    font-size: 14px; 
    margin: 0 auto;
  }
  
  .logbtn {
    background-color: #ea5c70;
    color: #fff;
  }
  </style>
  