<template>
    <div id="app">
      <Header title="Create account"/>
      <b-form @submit="onSubmit">
        <b-form-group label="Email address:" label-for="email" class="input"><br>
          <b-form-input id="email" v-model="form.email" type="email"
  placeholder="Enter email" required class="sminput"></b-form-input><br>
        </b-form-group>
        <b-form-group label="User Name:" label-for="name" class="input"><br>
          <b-form-input id="name" v-model="form.name" 
  placeholder="Enter name" required class="sminput"></b-form-input><br>
        </b-form-group>
        <b-form-group label="Password:" label-for="password" class="input"><br>
          <b-form-input id="password" v-model="form.password" 
  type="password" required class="sminput"></b-form-input><br>
        </b-form-group><br>
        <b-button type="submit" variant="primary" class="logbtn">Register</b-button>
      </b-form>
    </div>
  </template>
  <script>
    import Header from '@/components/Header.vue';
    import { mapActions } from 'vuex';
    export default {
      name: 'Register',
      components: {
        Header
      },
      data() {
        return {
          form: {
            email: '',
            name: '',
            password: '',
          }
        }
      },
      methods: {
        ...mapActions([
          'register'
        ]),
        onSubmit(e) {
          e.preventDefault();
          this.register(this.form);
          this.$router.push({ name: 'Home' });
        }
      }
    }
  </script>
   <style scoped>
   .input {
     text-align: center;
     font-size: large;
     
   }
   
   .sminput {
     width: 250px; 
     font-size: 14px; 
     margin: 0 auto;
   }
   
   .logbtn {
     background-color: #ea5c70;
     color: #fff;
   }
   </style>
   
  