<template>
  <div>
      <div v-if="usluga">
          <div id="usluga">
              <Header :title="usluga.naziv"/>
              <img class="slika" v-if="usluga.slika" :src="'http://localhost:9000/' + usluga.slika" />
              <p v-else>Nema slike</p> 
          </div>
          <div class="info">
              <h3>Detalji usluge:</h3>
              <div v-if="usluga.opis">
                  <p>{{ usluga.opis }} </p>
              </div>
              <h3>Kategorija usluge:</h3>
              <div v-if="usluga.kategorija_id">
                  <p>{{ mapaKategorije[usluga.kategorija_id] }}</p>
              </div>
              <h3>Cena usluge:</h3>
              <div v-if="usluga.cena">
                  <p>{{ usluga.cena }} RSD </p>
              </div>
              <div v-else>
                  <p>Cena: {{ usluga.cena }} RSD</p>
              </div>
          </div>
      </div>
      <div v-else>
          Loading...
      </div>
  </div>
</template>

  <script>
  import Header from '@/components/Header.vue'
 import { mapActions, mapState } from 'vuex'
 
  
  
  export default {
    name: 'Usluga',
    components: {
      Header
    },
    data(){
      return{
        usluga: null,
        mapaKategorije: {
      1: "Kuca",
      2: "Stan",
      3: "Poslovni prostor"
    }
      }
    },
    methods: {
    ...mapActions(['getUsluga'])
  },
  mounted(){
    this.getUsluga(this.$route.params.id)
      .then( res => {
        this.usluga = res;
      });
  }

  }
  </script>

<style scoped>
#usluga {
  font-family: 'Glock', serif;
  color: #ea5c70;
  margin-top: 60px;
  background-color: #502029;
  text-align: center;
}

.info {
  overflow: auto;
  text-align: center; 
  color: #ffffff;
  padding: 20px; 
  border-radius: 10px; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); 
  margin-top: 20px;
}

.info h3 {
  font-size: 1.5em;
  font-weight: bold;
  color: #ea5c70; 
}

.info p {
  margin-top: 10px;
  font-size: 1.2em;
  border: 1px solid #ffffff;
  padding: 8px;
  border-radius: 5px;
}
.slika {
  width: 100%;
  height: 450px;
  object-fit: cover;
  opacity: 0.4;
  border: 2px solid #ea5c70;
  margin-top: 20px;
}

.detalji {
  margin-top: 20px;
  background-color: #502029;
  padding: 15px;
  border-radius: 10px;
}

.detalji img {
  width: 200px;
  margin-bottom: 20px;
}

.detalji p {
  margin-top: 10px;
  font-size: 1.2em;
  color: #ffffff;
}

</style>
 
  